import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useStudentInstituteCharges } from "../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import { get_initiate_paytmV2 } from "../../../Decrypt/paytmUpiInitiative";
import PaymentGatewayConfig from "../../../PaymentGateway/PaymentGatewayConfig";
import QvipleLoader from "../../Loader/QvipleLoader";
const StudentPaymentGateway = ({
  onClose,
  instituteId,
  studentId,
  certificateType,
  certificateContent,
  certificateCharges,
  isOriginal,
}) => {
  const params = useParams();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    amount: 0,
    type: "Admission",
    isTransaction: false,
  });
  const { studentInstituteCharges } = useStudentInstituteCharges({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (certificateCharges) {
      setState({
        amount: certificateCharges ?? 0,
        type: "Admission",
        isTransaction: false,
      });
    }
  }, [certificateCharges]);

  const checkoutHandler = () => {
    if (studentInstituteCharges?.platform_charges?.percentage >= 0) {
      let gatewayCharges = (+state?.amount * 2.1) / 100;
      let gst = (+gatewayCharges * 18) / 100;
      let withGst = gatewayCharges + gst;
      const data = parseInt(state?.amount);
      var paymentData = {
        amount: Math.ceil(data + +withGst.toFixed(2)),
      };
      get_initiate_paytmV2({
        amount: +state?.amount ?? 10,
        amount_nocharges: +paymentData.amount ?? 0,
        type: "Certificate",
        paidBy: studentId,
        paidTo: instituteId,
        moduleId: null,
        ad_status_id: null,
        payment_installment: null,
        ad_install: null,
        month: null,
        payment_remain_1: null,
        payment_card_type: null,
        payment_book_id: null,
        name: params.username,
        isApk: "web",
        payment_card_id: null,
        payment_remain_fees: null,
        charge: {
          num_platform_percent:
            studentInstituteCharges?.platform_charges?.percentage ?? 0,
          num_platform_max:
            studentInstituteCharges?.platform_charges?.maximum ?? 0,
          num_trans_pecent:
            studentInstituteCharges?.transaction_charges?.percentage ?? 0,
          num_trans_max:
            studentInstituteCharges?.transaction_charges?.maximum ?? 0,
          num_app_max: studentInstituteCharges?.application_charges ?? 0,
        },
        cert_type: certificateType,
        cert_content: certificateContent,
        is_original: isOriginal,
        nsid: null,
      })
        .then((data) => {
          // console.info("ldskjfnsbkjasdnf,ms", data);
          window.onScriptLoad(
            data?.response?.body?.txnToken,
            data?.order,
            data?.amount
          );
        })
        .catch((e) => {
          console.info(e);
        });
    }
  };
  // console.info("studentInstituteCharges", certificateCharges);

  return (
    <>
      <PaymentGatewayConfig
        onClose={onClose}
        allContent={{
          ...state,
          platform_charges:
            studentInstituteCharges?.platform_charges?.percentage,
        }}
        checkoutAdmissionHandler={checkoutHandler}
        isOptimzed
      />

      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentPaymentGateway;
